.ant-layout-header{
    background: #fff !important;
    height: 48px !important;
}
.ant-layout-header .ant-menu {
    line-height: 47px !important;
}
.ant-form-vertical .ant-form-item-label{
    padding: 0 !important;
}
.ant-form-item-label > label{
    font-weight: 500 !important;
}
.ant-form-item{
    margin-bottom: 12px !important;
}
.login-content {
    background: #fff;
    box-shadow: 0 1px 11px rgba(0, 0, 0, 0.27);
    border-radius: 2px;
    width: 100%;
    display: inline-block;
    margin-top: 20px;
    vertical-align: middle;
    position: relative;    
    padding: 35px;
}

.social-login > a {
    padding-right: 10px;
    font-size: 24px;
}

.signup-link {
    margin-top: 1em;
    color: rgba(0, 0, 0, 0.65);
    font-size: 14px;
    text-align: center;
}

.login-title {
    font-size: 1.5em;
    font-weight: 500;
    margin-top: 0;
    margin-bottom: 30px;
    color: rgba(0, 0, 0, 0.65);
}